import { clipboard, dialog, Menu, MenuItemConstructorOptions, shell } from 'electron'
import { existsSync } from 'fs'
import { pathInfo } from './global'
import { checkUpdate } from './update/doUpdate'
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { sureRestart } from './utils/notify'
import { showErrorMessage } from './utils/notify'
import { getProjectSettings, getCurrentVersionKey } from './utils/settings'
import process from 'process'

const os = require('os')
declare const __BUILD_INFO__: string
declare const __COMMIT_HASH__: string

// const devLabel = process.platform === 'darwin' ? '' : ''

const getMenuTemplate: MenuItemConstructorOptions[] = [
    {
        label: '',
        submenu: [
            {
                role: 'toggleDevTools',
                label: '',
            },
            {
                role: 'togglefullscreen',
                label: '',
            },
            {
                label: '',
                click: () => {
                    const { exeRoot } = pathInfo
                    if (existsSync(exeRoot)) {
                        shell.openPath(exeRoot).catch(e => {
                            showErrorMessage(e.message ? e.message : '')
                        })
                    }
                },
            },
            {
                role: 'quit',
                label: '',
            },
        ],
    },
    {
        role: 'reload',
        label: '',
    },
    // {
    //     role: 'toggleDevTools',
    //     label: '',
    // },
    {
        label: '',
        submenu: [
            {
                label: '',
                click: () => {
                    checkUpdate()
                },
            },
            {
                role: 'reload',
                label: '',
            },
            {
                label: '',
                click: () => {
                    sureRestart('')
                },
            },
            {
                label: '',
                click: () => {
                    const settings = getProjectSettings()
                    const aboutInfo = `
1. : ${settings[getCurrentVersionKey(settings.currentBranch)]}
2. : ${settings.currentBranch}
3. : ${__BUILD_INFO__}
4. Electron: ${process.versions.electron}
5. Node:${process.versions.node},
6. Chrome:${process.versions.chrome}
7. Commit: ${__COMMIT_HASH__}`
                    dialog
                        .showMessageBox({
                            title: '',
                            message: aboutInfo,
                            buttons: ['', ''],
                        })
                        .then(v => {
                            const resp = v.response
                            if (resp === 1) {
                                clipboard.writeText(aboutInfo)
                            }
                        })
                        .catch(() => {})
                },
            },
        ],
    },
]
// mac
if (os.platform() === 'darwin') {
    getMenuTemplate.push({
        label: 'Edit',
        submenu: [
            { role: 'undo' },
            { role: 'redo' },
            { type: 'separator' },
            { role: 'cut' },
            { role: 'copy' },
            { role: 'paste' },
            { role: 'delete' },
        ],
    })
}
const menu = Menu.buildFromTemplate(getMenuTemplate)

/**
 * 
 */
export function setMenu() {
    Menu.setApplicationMenu(menu)
}
